  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

.visually-hidden {
  overflow: hidden;
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
}

.clearfix:before, .clearfix:after,
.l-mobile-header:before,
.l-footer-inner:before,
.l-constrained:before,
.l-pre-header:before,
.l-header:before,
.l-inner:before,
.l-content:before,
#content:before,
.pre-content:before,
.l-content-wrapper:before,
.l-pre-footer-wrapper:before,
.l-footer-wrapper:before,
.l-mobile-header:after,
.l-footer-inner:after,
.l-constrained:after,
.l-pre-header:after,
.l-header:after,
.l-inner:after,
.l-content:after,
#content:after,
.pre-content:after,
.l-content-wrapper:after,
.l-pre-footer-wrapper:after,
.l-footer-wrapper:after {
  content: "";
  display: table;
}

.clearfix:after,
.l-mobile-header:after,
.l-footer-inner:after,
.l-constrained:after,
.l-pre-header:after,
.l-header:after,
.l-inner:after,
.l-content:after,
#content:after,
.pre-content:after,
.l-content-wrapper:after,
.l-pre-footer-wrapper:after,
.l-footer-wrapper:after {
  clear: both;
  font-size: 0;
  /* Clear Fix correction (add font-size to fix extra padding at bottom of site */
}

