/*---------------------------------------------------------
-----------------------------------------------------------

 General LINK styles

-----------------------------------------------------------
----------------------------------------------------------*/
a,
a:link,
a:visited,
li a.active {
  -webkit-transition: all 0.2s ease-in;
  -moz-transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
  color: #64BAFF;
  text-decoration: none;
}

a:hover,
a:link:hover,
a:visited:hover,
li a.active:hover {
  color: #222222;
  text-decoration: underline;
}

