.grid {
  margin: 0 0 3rem 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.grid li,
.grid .grid-item {
  list-style-type: none;
}

@media (min-width: 0em) and (max-width: 40em) {
  .grid li,
.grid .grid-item {
    width: 100%;
    margin: 0 0 1em 0;
  }
}
@media (min-width: 40.063em) {
  .grid li,
.grid .grid-item {
    width: 48%;
    margin: 0 3.8% 4% 0;
  }

  .grid li:nth-child(2n+0),
.grid .grid-item:nth-child(2n+0) {
    margin-right: 0;
  }
}
@media (min-width: 64.063em) {
  .grid li,
.grid .grid-item {
    width: 30%;
    margin: 0 4.9% 5% 0;
  }

  .grid li:nth-child(3n+0),
.grid .grid-item:nth-child(3n+0) {
    margin-right: 0;
  }
}
