  .grid li,
.grid .grid-item {
    width: 22%;
    margin: 0 3.8% 4% 0;
  }

  .grid li:nth-child(4n+0),
.grid .grid-item:nth-child(4n+0) {
    margin-right: 0;
  }
}
.css-grid {
  margin: 0 0 3rem 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

@supports (display: grid) {
  .css-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 40px 3.8%;
  }
}
.css-grid li,
.css-grid .grid-item {
  list-style-type: none;
  padding: 0;
  margin: 0;
  position: relative;
}

@supports not (display: grid) {
  @media (min-width: 0em) and (max-width: 40em) {
    .css-grid .grid-item {
      width: 100%;
      margin: 0 0 1em 0;
    }
  }
  @media (min-width: 40.063em) {
    .css-grid .grid-item {
      width: 48%;
      margin: 0 3.8% 4% 0;
    }

    .css-grid .grid-item:nth-child(2n+0) {
      margin-right: 0;
    }
  }
  @media (min-width: 64.063em) {
    .css-grid .grid-item {
      width: 30%;
      margin: 0 4.9% 5% 0;
    }

    .css-grid .grid-item:nth-child(3n+0) {
      margin-right: 0;
    }
  }
  @media (min-width: 90.063em) {
    .css-grid .grid-item {
      width: 22%;
      margin: 0 3.8% 4% 0;
    }

    .css-grid .grid-item:nth-child(4n+0) {
      margin-right: 0;
    }
  }
}
